<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { decrypt2 } from '@/common/secure'
import { ImagePreview } from 'vant'
import { getProductInfo, getShdz, getLessStock, getOrderList, getInvoiceList, getInvoiceAddressList, getFreight, getYsAddress,
  placeOrder, payOrder, deleteAccessory, validateOrderApproal } from '@/store/module.js'
import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'ConfirmOrder',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      pjShow: false,
      productList: [],
      addressClass: [],
      shdzList: [],
      adddressSfkxg: false,
      ysDiv: false,
      e_address_id: '',
      freight: 0,
      address: {
        province: '',
        city: '',
        county: '',
        town: ''
      },
      dsbsArr: [],
      dsbsYsArr: [],
      showAccessory: [],
      dsbs: '', // 商品类型
      num: '',
      ruleForm: {
        remark: '',
        sy: ''

        // ysList: [{ ysx: '预算项', ysdm: '', ysje: '' }]
      },
      remarkForm: {
        gysRemarkList: [],
        ysList: []
      },
      btnName: '结算',
      sfjs: false,
      ly: '', // 留言
      show4: false,
      defaultAddressInvoice: '',
      orderInvoiceList: [], // 确认发票地址列表
      show3: false,
      invoiceList: [], // 发票地址列表
      show2: false,
      cartList: [], // 商品信息
      cart_ids: [], // 多个商品id
      e_product_id: '', // 商品id
      addresList: [], // 收货地址列表
      show1: false,
      value: '',
      active: '',
      checked: true,
      show: false,
      radio: '1',
      defaultAddress: '',
      defaultInvoice: '',
      invoiceId: '',
      addressId: '',
      headerData: {
        title: '确认订单',
        leftUrl: 'back',
        leftText: ' '
      },
      gysmc: '',
      gysbh: '',
      accessoryIdsList: []
    }
  },
  computed: {
    priceSum: function() {
      let priceSum = 0
      for (const cart of this.cartList) {
        for (const item of cart.productList) {
          priceSum += parseInt(item.pcount) * item.price_now
          if (item.accessoryList != null) {
            for (const accessory of item.accessoryList) {
              priceSum += parseFloat(accessory.price).toFixed(2) * parseInt(accessory.count)
            }
          }
        }
      }
      priceSum += this.freight
      return priceSum.toFixed(2)
    }
  },
  activated() {
    this.getShdzList()
    this.getInvoiceList()
  },
  created() {
    if (this.$store.state.bid.xjxx && this.$store.state.bid.xjxx.bjlsSpxxList) {
      for (let i = 0; i < this.$store.state.bid.xjxx.bjlsSpxxList.length; i++) {
        this.cart_ids.push(this.$store.state.bid.xjxx.bjlsSpxxList[i].cart_id)
      }
    }
    if (this.$store.state.goods.query) {
      this.e_product_id = decrypt2(this.$store.state.goods.query.e_product_id)
    }
    if (this.$store.state.goods.query && this.$store.state.goods.query.cart_ids) {
      this.cart_ids = decrypt2(this.$store.state.goods.query.cart_ids).split(',')
    }
    if (this.$store.state.goods.query && this.$store.state.goods.query.gysbh) {
      this.gysbh = decrypt2(this.$store.state.goods.query.gysbh)
    }
    if (this.$store.state.goods.query && this.$store.state.goods.query.gysmc) {
      this.gysmc = decodeURIComponent(decrypt2(this.$store.state.goods.query.gysmc))
    }
    if (this.$store.state.goods.query && this.$store.state.goods.query.num) {
      this.num = decrypt2(this.$store.state.goods.query.num)
    }
    if (this.$store.state.goods.query && this.$store.state.goods.query.accessoryIdsList) {
      this.accessoryIdsList = decrypt2(this.$store.state.goods.query.accessoryIdsList).split(',')
    }
    if (this.$store.state.goods.query && this.$store.state.goods.query.accessoryName) {
      this.accessoryName = this.$store.state.goods.query.accessoryName
    }
    if (this.$store.state.goods.query && this.$store.state.goods.query.accessoryPrice) {
      this.accessoryPrice = decrypt2(this.$store.state.goods.query.accessoryPrice).split(',')
    }
    // if (this.$store.state.goods.spid) {
    //   this.e_product_id = this.$store.state.goods.spid
    // }
    if (this.$store.state.goods.checkCart.length > 0) {
      this.cart_ids = this.$store.state.goods.checkCart
    }
    // this.getAdressList()
    this.getProductList()
    this.getInvoiceList()
    // this.getOrderInvoiceList()
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    pjisShow() {
      this.pjShow = !this.pjShow
    },
    goAddAddressInvoice() {
      this.show3 = false
      this.$router.push('InvoiceAddressAdd')
    },
    goAddInvoice() {
      this.$router.push('InvoiceAdd')
    },
    orderInvoiceChange(addressInvoice) {
      this.show3 = false
      this.defaultAddressInvoice = addressInvoice
    },
    // 获取订单发票地址
    getOrderInvoiceList() {
      getInvoiceAddressList.data = { pageno: 1, pagerow: 200 }
      this.asyncApiModule(getInvoiceAddressList).then(res => {
        this.orderInvoiceList = res.list
        for (let i = 0; i < this.orderInvoiceList.length; i++) {
          const item = this.orderInvoiceList[i]
          if (item.isdefault === 1) {
            this.defaultAddressInvoice = item
          }
        }
      })
        .catch(() => {
          this.loading = false
        })
    },
    invoiceChange(invoice) {
      this.defaultInvoice = invoice
      this.invoiceId = invoice.id
      this.show2 = false
    },
    // 获取发票地址列表
    getInvoiceList() {
      getInvoiceList.data = { pageno: '', pagerow: '' }
      this.asyncApiModule(getInvoiceList).then(res => {
        this.invoiceList = res.list
        if (this.invoiceList.length > 0) {
          this.defaultInvoice = this.invoiceList[0]
          this.invoiceId = this.defaultInvoice.id
        }
      })
        .catch(() => {
          this.loading = false
        })
    },
    goAddAddress() {
      this.$router.push({
        name: 'AddressAdd',
        params: {
          title: '新增收货地址'
        }
      })
    },
    // 跳转商品详情
    toProductDetail(product_id, spmc) {
      this.$store.commit('goods/SET_SPMC', spmc)
      this.$store.commit('goods/SET_SPID', product_id)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    deleteAccessory(accessory_id) {
      deleteAccessory.data = { e_cart_accessory_id: accessory_id }
      this.asyncApiModule(deleteAccessory).then(res => {
        this.getProductList()
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取商品列表
    getProductList() {
      if (this.e_product_id) {
        getProductInfo.data = { e_product_id: this.e_product_id }
        this.asyncApiModule(getProductInfo).then(res => {
          this.dsbs = res.e_product.dsbs
          var node = {
            id: res.e_product.id,
            name: res.e_product.name,
            dsbs: res.e_product.dsbs,
            category_name_full: res.catename,
            imgSrc: res.imgsList[0],
            price_now: res.e_product.price_now,
            pcount: this.num,
            accessoryPrice: 0,
            accessoryList: [],
            ysIndex: 0,
            cart_id: ''
          }
          const remark = {
            gysbh: '',
            remark: ''
          }
          const ys = {
            ysx: res.e_product.ysx ? res.e_product.ysx : '',
            ysbm: res.e_product.ysbm ? res.e_product.ysbm : '',
            subTotalPrice: parseFloat(node.price_now * node.pcount).toFixed(2),
            productId: node.id,
            productName: node.name
          }

          if (this.dsbs === 'KJXY') {
            remark.gysbh = this.gysbh
          } else {
            remark.gysbh = res.gysbh
          }
          this.remarkForm.gysRemarkList.push(remark)
          this.remarkForm.ysList.push(ys)
          let accessoryPrice = 0
          for (var i = 0; i < this.accessoryIdsList.length; i++) {
            var accessory = {
              name: this.accessoryName[i],
              price: this.accessoryPrice[i],
              count: this.num
            }
            accessoryPrice = accessoryPrice + parseFloat(this.accessoryPrice[i])
            node.accessoryList.push(accessory)
            node.accessoryPrice = accessoryPrice
          }
          this.productList.push(node)
          this.showAccessory.push(false)
          var cart = {
            dsbs: this.dsbs,
            gysmc: '',
            productList: this.productList,
            jklx: res.jklx

          }
          // if (this.dsbs !== 'KJXY') {
          //   if (!res.iscomparision) {
          //     this.sfValiBeiz = true
          //     this.valiInfo = this.valiInfo + '【' + node.name + '】' + '未进行比价'
          //   }
          //   if (res.iscomparision && !res.iszdj) {
          //     this.sfValiBeiz = true
          //     this.valiInfo = this.valiInfo + '【' + node.name + '】' + '在比价列表中不是最低价'
          //   }
          // }
          if (this.dsbs === 'KJXY') {
            cart.gysmc = this.gysmc
          } else {
            cart.gysmc = res.gysmc
          }
          if (this.dsbs !== 'KJXY') {
            this.dsbsArr.push(this.dsbs)
          }
          this.cartList.push(cart)
          this.computeFreight()
          this.ysAddress()
        })
      } else if (this.cart_ids.length > 0) {
        getOrderList.data = { e_carts: this.cart_ids }
        this.asyncApiModule(getOrderList).then(res => {
          this.cartList = res
          let index = 0
          for (const cart of this.cartList) {
            if (cart.dsbs !== 'KJXY' && this.dsbsArr.indexOf(cart.dsbs) === -1) {
              this.dsbsArr.push(cart.dsbs)
            }
            const remark = {
              gysbh: cart.gysbh,
              remark: ''
            }
            cart.valiRemark = false
            this.remarkForm.gysRemarkList.push(remark)
            this.showAccessory.push(false)
            for (const proInfo of cart.productList) {
              const ys = {
                ysx: proInfo.ysx ? proInfo.ysx : '',
                ysbm: proInfo.ysbm ? proInfo.ysbm : '',
                subTotalPrice: proInfo.AMOUNT,
                productId: proInfo.id,
                productName: proInfo.name
              }
              this.remarkForm.ysList.push(ys)
              // if (proInfo.dsbs !== 'KJXY') {
              //   if (!proInfo.iscomparision) {
              //     this.sfValiBeiz = true
              //     this.valiInfo = this.valiInfo + '，' + '【' + proInfo.name + '】' + '未进行比价'
              //   }
              //   if (proInfo.iscomparision && !proInfo.iszdj) {
              //     this.sfValiBeiz = true
              //     this.valiInfo = this.valiInfo + '，' + '【' + proInfo.name + '】' + '在比价列表中不是最低价'
              //   }
              // }

              let accePrice = 0
              if (proInfo.accessoryList) {
                for (const acce of proInfo.accessoryList) {
                  accePrice = accePrice + acce.price
                }
              }
              proInfo.accessoryPrice = accePrice
              proInfo.ysIndex = index
              index++
            }
          }
          if (this.valiInfo) {
            this.valiInfo = this.valiInfo.substring(1, this.valiInfo.length)
          }
          this.computeFreight()
          // this.$refs.GoodsAddress.getYsAddress(this.dsbsArr)
          this.ysAddress()
          if (this.$store.state.bid.xjxx) {
            const spxxlist = this.$store.state.bid.xjxx.bjlsSpxxList
            const cartlist = this.cartList
            if (spxxlist && spxxlist.length > 0) {
              for (let k = 0; k < spxxlist.length; k++) {
                for (let i = 0; i < cartlist.length; i++) {
                  const item = cartlist[i].productList
                  for (let j = 0; j < item.length; j++) {
                    if (item[j].id === spxxlist[k].id) {
                      item[j].price_now = spxxlist[k].dj
                      item[j].pcount = spxxlist[k].cgsl
                    }
                  }
                }
              }
            }
          }
        })
      }
    },
    ysAddress() {
      this.getYsAddress(this.dsbsArr)
    },
    // 获取配送费
    computeFreight(province, city, county, town) {
      if (province) {
        this.address.province = province
        this.address.city = city
        this.address.county = county
        this.address.town = town
        if (town === null) {
          this.address.town = 0
        }
      }
      if (this.address.province && this.cartList.length > 0) {
        var productInfo = {
          province: this.address.province,
          city: this.address.city,
          county: this.address.county,
          town: this.address.town,
          dsbs: '',
          skuNums: []
        }
        this.freight = 0
        for (const cart of this.cartList) {
          if (cart.jklx !== 2) {
            var proArr = []
            for (const item of cart.productList) {
              var product = { skuId: item.id, num: item.pcount }
              proArr.push(product)
            }
            productInfo.skuNums = proArr
            productInfo.dsbs = cart.dsbs
            getFreight.data = productInfo
            this.asyncApiModule(getFreight).then(res => {
              this.freight = this.freight + res.freight
            })
              .catch(() => {
                this.loading = false
              })
          }
        }
      }
    },
    // 获取映射地址
    getYsAddress(dsbsArr) {
      this.getShdzList()
      if (!dsbsArr && this.dsbsYsArr) {
        dsbsArr = this.dsbsYsArr
      }
      if (dsbsArr && dsbsArr.length > 0) {
        this.dsbsArr = dsbsArr
      }
      if (this.dsbsArr.length > 0 && this.e_address_id) {
        this.dsbsYsArr = this.dsbsArr
        this.ysDiv = true
        var address1 = {
          e_address_id: this.e_address_id,
          dsbs: this.dsbsArr
        }
        getYsAddress.data = address1
        this.asyncApiModule(getYsAddress).then(res => {
          this.ysAddressList = []
          for (const address of res) {
            var ysAddress = {
              id: '',
              dsbs_value: '',
              address: '',
              area: [],
              placeholder: ''
            }
            ysAddress.dsbs_value = address.dsbs_value
            ysAddress.dsbs = address.dsbs
            ysAddress.address = address.address

            const areaArr = []
            if (address.province) {
              areaArr.push(address.province)
              ysAddress.placeholder = ysAddress.placeholder + address.provinceValue
            }
            if (address.city) {
              areaArr.push(address.city)
              ysAddress.placeholder = ysAddress.placeholder + '/' + address.cityValue
            }
            if (address.county) {
              areaArr.push(address.county)
              ysAddress.placeholder = ysAddress.placeholder + '/' + address.countyValue
            }
            if (address.town) {
              areaArr.push(address.town)
              ysAddress.placeholder = ysAddress.placeholder + '/' + address.townValue
            }
            ysAddress.area = areaArr
            this.ysAddressList.push(ysAddress)
          }
        })
          .catch(() => {
            this.loading = false
          })
      }
    },
    addressChange(address) {
      this.defaultAddress = address
      this.addressId = address.id
      this.show1 = false
      this.computeFreight(address.province, address.city, address.county, address.town)
    },
    // 获取收货地址
    getShdzList() {
      getShdz.data = { pageno: '', pagerow: '' }
      this.asyncApiModule(getShdz).then(res => {
        this.addresList = res.list
        for (let i = 0; i < this.addresList.length; i++) {
          const address = this.addresList[i]
          if (address.default_address === 1) {
            this.defaultAddress = address
            this.addressId = address.id
          }
        }
        if (this.$route.params.address) {
          this.defaultAddress = this.$route.params.address
          this.addressId = this.defaultAddress.id
        } else if (!this.defaultAddress && this.addresList.length > 0) {
          this.defaultAddress = this.addresList[0]
          this.addressId = this.defaultAddress.id
        }
        this.address = this.defaultAddress

        if (res.fhdzsfty === 'Y') {
          this.adddressSfkxg = false
        } else {
          this.adddressSfkxg = true
        }
        this.shdzList = res.list
        let address2
        this.addressClass = []
        if (this.shdzList.length > 0) {
          for (let i = 0; i < this.shdzList.length; i++) {
            this.addressClass.push('address-list')
            if (this.e_address_id !== '' && this.e_address_id === this.shdzList[i].id) {
              this.addressClass[i] = 'address-list active'
              address2 = this.shdzList[i]
            }
          }

          if (this.e_address_id === '') {
            for (let i = 0; i < this.shdzList.length; i++) {
              if (this.shdzList[i].default_address) {
                this.e_address_id = this.shdzList[i].id
                address2 = this.shdzList[i]
                this.addressClass[i] = 'address-list active'
              }
            }
          }
          if (this.e_address_id === '') {
            this.e_address_id = this.shdzList[0].id
            address2 = this.shdzList[0]
            this.addressClass[0] = 'address-list active'
          }
        } else {
          this.e_address_id = ''
        }
        if (this.e_address_id) {
          this.initAddress(address2.province, address2.city, address2.county, address2.town)
        }
      })
        .catch(() => {
          this.loading = false
        })
    },
    initAddress: function(province, city, county, town) {
      this.address.province = province
      this.address.city = city
      this.address.county = county
      this.address.town = town
      if (town == null) {
        this.address.town = 0
      }
      this.computeFreight()
    },
    // 获取收货地址
    // getAdressList() {
    //   this.loading = true
    //   this.$store
    //     .dispatch('member/getAddressListAction', { pageno: 1,
    //       pagerow: 1000000 })
    //     .then(res => {

    //     })
    //     .catch(() => {
    //       this.loading = false
    //     })
    // },
    showPopupDz() {
      this.show1 = true
    },
    onClose() {

    },
    goBack() {
      this.$router.back()
    },
    goAddresslist() {
      this.show3 = true
      // this.$router.push({
      //   name: 'AddressList'
      // })
    },
    showPopup() {
      this.show2 = true
    },
    changeAccessory(index, indexpro) {
      if (this.showAccessory[index + indexpro]) {
        this.showAccessory.splice(index + indexpro, 1, false)
      } else {
        this.showAccessory.splice(index + indexpro, 1, true)
      }
    },
    // 结算
    async getLessStock() {
      console.log('this.cartList-------------------------------------------------------', this.cartList)
      if (!this.cartList || this.cartList.length === 0) {
        this.$toast('请选择购买商品！')
        return
      }
      if (!this.defaultAddress) {
        this.$toast('请选择收货地址！')
        return
      }
      if (!this.defaultInvoice) {
        this.$toast('请选择发票信息！')
        return
      }
      this.$validator.validate().then(result => {
        if (result) {
          this.sfjs = true
          this.btnName = '提交中...'
        }
      })
      if (this.ruleForm.sy && this.ruleForm.remark) {
        var productInfo = {
          province: this.defaultAddress.province,
          city: this.defaultAddress.city,
          county: this.defaultAddress.county,
          town: this.defaultAddress.town,
          produceList: [],
          dsbs: ''
        }
        let stock = true
        var isToOrder = true
        for (const cart of this.cartList) {
          for (var i = 0; i < cart.productList.length; i++) {
            for (const proInfo of cart.productList) {
              if (proInfo.status === 3) {
                isToOrder = false
              }
            }
          }
          if (!isToOrder) {
            this.$toast.fail('本次交易中含有已下架产品，无法继续购买')
            this.btnName = '结算'
            this.sfjs = false
            return
          }
          if (cart.dsbs !== 'KJXY' && cart.jklx !== 2) {
            var proArr = []
            for (const item of cart.productList) {
              var product = { skuId: item.id, num: item.pcount }
              proArr.push(product)
            }
            if (proArr.length > 0) {
              productInfo.produceList = proArr
              productInfo.dsbs = cart.dsbs
              getLessStock.data = productInfo
              await this.asyncApiModule(getLessStock).then(res => {
                for (var key in res) {
                  if (key !== 'success') {
                    stock = false
                    this.$toast.fail(res[key])
                    this.btnName = '结算'
                    return
                  }
                }
              })
                .catch(() => {
                  this.loading = false
                })
            }
          }
        }
        if (stock) {
          this.settlement()
        } else {
          this.btnName = '结算'
          this.sfjs = false
        }
      } else {
        this.$toast.fail('请完善页面信息')
      }
    },
    settlement() {
      const valiInfo = {}
      // const valiList = []
      // for (const cart of this.cartList) {
      //   for (const pro of cart.productList) {
      //     const vali = { productId: pro.id, ysbm: pro.ysbm, productName: pro.name, subTotalPrice: pro.AMOUNT }
      //     valiList.push(vali)
      //   }
      // }
      valiInfo.list = this.remarkForm.ysList
      validateOrderApproal.data = valiInfo

      this.asyncApiModule(validateOrderApproal).then(res => {
        if (res.success === true) {
          if (this.e_product_id) {
            const orderInfo = {
              e_address_id: '',
              e_invoice_id: '',
              e_invoice_address_id: '',
              sy: this.ruleForm.sy,
              remark: this.ruleForm.remark,
              gysremark: '',
              e_order_invoice_invoicestate: '',
              e_product_id: this.e_product_id,
              accessoryIdsList: this.accessoryIdsList,
              dsbs: this.dsbs,
              num: this.num,
              e_supplier_id: this.remarkForm.gysRemarkList[0].gysbh,
              ysbm: this.remarkForm.ysList[0].ysbm,
              ysx: this.remarkForm.ysList[0].ysx
            }

            orderInfo.gysremark = this.remarkForm.gysRemarkList[0].remark
            orderInfo.e_address_id = this.defaultAddress.id
            orderInfo.e_invoice_id = this.defaultInvoice.id
            if (this.checked) {
              orderInfo.e_invoice_address_id = '_no_e_invoice_address_id'
            } else {
              orderInfo.e_invoice_address_id = this.defaultAddressInvoice.id
            }
            payOrder.data = orderInfo
            this.asyncApiModule(payOrder).then(res => {
              let succ = true
              for (const result of res) {
                if (!result.success) {
                  succ = false
                }
              }
              // let e_approval_id = ''
              if (succ) {
                if (res[0].e_order.e_approval_id) {
                  // e_approval_id = res[0].e_order.e_approval_id
                }
                // let priceCount = 0
                const ddbhArray = []
                for (const order of res) {
                  if (order.e_order) {
                    // priceCount += order.e_order.order_amount
                    ddbhArray.push(order.e_order.ddbh)
                  }
                }
                this.$toast.success('下单成功')
                setTimeout(() => {
                  this.$toast.clear()
                  this.$router.back()
                }, 1000)
                // this.$router.push({
                //   name: 'goodsOrderSuccess',
                //   query: {
                //     price: encrypt(priceCount),
                //     ddbh: encrypt(ddbhArray),
                //     e_approval_id: encrypt(e_approval_id)
                //   }
                // })
              } else {
                // this.sfjs = false
                this.$toast.fail('下单失败，请联系管理员')
                this.btnName = '结算'
              }
            })
              .catch(() => {
                this.$toast.fail('下单失败，请联系管理员')
                this.btnName = '结算'
                // this.sfjs = false
                this.loading = false
              })
          } else {
            const orderInfo = {
              e_address_id: '',
              e_invoice_id: '',
              e_invoice_address_id: '',
              sy: this.ruleForm.sy,
              remark: this.ruleForm.remark,
              e_cart_idslist: this.cart_ids,
              gysRemarkList: this.remarkForm.gysRemarkList,
              e_order_invoice_invoicestate: ''
            }
            const productDetaillist = []
            for (const ys of this.remarkForm.ysList) {
              const vali = { e_product_id: ys.productId, ysbm: ys.ysbm, ysx: ys.ysx }
              productDetaillist.push(vali)
            }
            orderInfo.productDetaillist = productDetaillist
            orderInfo.e_address_id = this.defaultAddress.id
            orderInfo.e_invoice_id = this.defaultInvoice.id
            if (this.checked) {
              orderInfo.e_invoice_address_id = '_no_e_invoice_address_id'
            } else {
              orderInfo.e_invoice_address_id = this.defaultAddressInvoice.id
            }
            placeOrder.data = orderInfo
            this.asyncApiModule(placeOrder).then(res => {
              let succ = true
              for (const result of res) {
                if (!result.success) {
                  succ = false
                }
              }

              if (succ) {
                // let e_approval_id = ''
                if (res[0].e_order.e_approval_id) {
                  // e_approval_id = res[0].e_order.e_approval_id
                }
                // let priceCount = 0
                const ddbhArray = []
                for (const order of res) {
                  if (order.e_order) {
                    // priceCount += order.e_order.order_amount
                    ddbhArray.push(order.e_order.ddbh)
                  }
                }
                this.$toast.success('下单成功')
                setTimeout(() => {
                  this.$toast.clear()
                  this.$router.back()
                }, 1000)
                // this.$router.push({
                //   name: 'goodsOrderSuccess',
                //   query: {
                //     price: encrypt(priceCount),
                //     ddbh: encrypt(ddbhArray),
                //     e_approval_id: encrypt(e_approval_id)
                //   }
                // })
              } else {
                this.$toast('下单失败，请联系管理员')
                this.btnName = '结算'
              }
              // this.sfjs = false
            })
              .catch(() => {
                this.$toast('下单失败，请联系管理员')
                this.btnName = '结算'
                // this.sfjs = false
                this.loading = false
              })
          }
        } else {
          this.$toast(res.resultMessage)
          this.btnName = '结算'
          this.sfjs = false
        }
      })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
      // })
    },
    // 删除商品
    DeleteSp(index, id, cart_id) {
      for (var i = 0; i < this.cartList.length; i++) {
        var productList = this.cartList[i].productList
        for (var j = 0; j < productList.length; j++) {
          if (productList[j].id === id) {
            productList.splice(j, 1)
          }
        }
        if (productList.length === 0) {
          this.cartList.splice(i, 1)
        }
      }
      for (let i = 0; i < this.$refs.closeSwipe.length; i++) {
        this.$refs.closeSwipe[i].close()
      }
      for (let i = 0; i < this.cart_ids.length; i++) {
        if (this.cart_ids[i] === cart_id) {
          this.cart_ids.splice(i, 1)
        }
      }
    }
  }

}
</script>

